

@media (min-width: 700px) {
  /* web */
  .favorite-page {
    display: grid;
    grid-template-columns: max-content max-content max-content 159px;
    justify-content: space-between;
    padding-top: 10px;
  }
}

@media (max-width: 700px) {
  /* mobile */
  .favorite-page {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}

.favorite-city {
  display: flex;
 
  height: 140px;
  width: 150px;
  font-family: sans-serif;
}
.favorite-city:hover {
  cursor: grab;
  font-weight: bold;
}
.favorite-city p {
  display: flex;
  justify-content: center;
  font-size: 20px;
}

@media (min-width: 700px) {
  /* web */
  .favorite-city {
    flex-direction: column;
    border: 3px solid black;
  }
}

@media (max-width: 700px) {
  /* mobile */
  .favorite-city {
    flex-direction: row;
    justify-content: space-around;
    border: 1px solid black;
    width: 98.5%;
  }
}
